body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #16161d;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.tile {
  display: -webkit-inline-flex;
  display: inline-flex;
  background-color: #4f7942;
}

.rock {
  /* Development URL */
  /* background-image: url(/tiles/rock.png); */
  /* Production URL */
  background-image: url(/dc-game/tiles/rock.png);
}

.tree {
  /* Development URL */
  /* background-image: url(/tiles/tree.png); */
  /* Production URL */
  background-image: url(/dc-game/tiles/tree.png);
}

.chest {
  /* Development URL */
  /* background-image: url(/tiles/chest.png); */
  /* Production URL */
  background-image: url(/dc-game/tiles/chest.png);
}

.modal {
  position: absolute;
  top: 0;
  margin: 0 auto;
  width: 800px;
  height: auto;
  background-color: rgba(0, 0, 0, 0.8);
  border: 4px solid gray;
}

.modal h1 {
  color: white;
  margin-top: 0.5em;
  font-size: 2em;
  text-align: center;
}

.container {
  color: white;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
  -webkit-justify-content: flex-end;
          justify-content: flex-end;
}

.menu {
  width: 100px;
  height: 100px;
  position: relative;
  color: white;
  text-align: center;
}

.enemy {
  width: 250px;
}

.enemy img,
.menu img {
  max-width: 100%;
  max-height: 100%;
}

h4 {
  margin: 0;
}

.menu div {
  position: absolute;
  left: 15px;
  bottom: 0;
}

